      const res = await fetch(`${API_URL}/trees`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      const data = await res.json().catch(() => ([]));
      if (!res.ok) {
        throw new Error(data.message || 'Aalar alnamad.');
      }
      setTrees(data);
