/* -------------------- ANA APP -------------------- */

function App() {
  const [token, setToken] = useState(null);
  const [username, setUsername] = useState('');
  const [tab, setTab] = useState(() => {
    // Sayfa yklenirken localStorage'dan tab deerini oku
    const savedTab = localStorage.getItem('sg_current_tab');
    return savedTab || 'home';
  });
  const [mobileTabsOpen, setMobileTabsOpen] = useState(false); //  yeni
  const [pushEnabled, setPushEnabled] = useState(false);
  const [pushError, setPushError] = useState('');
  const [reminderMonth, setReminderMonth] = useState(
    new Date().getMonth() + 1
  );

  useEffect(() => {
    const storedToken = localStorage.getItem('sg_token');
    const storedUser = localStorage.getItem('sg_username');
    if (storedToken) setToken(storedToken);
    if (storedUser) setUsername(storedUser);

    if (typeof Notification !== 'undefined') {
      if (Notification.permission === 'granted') {
        setPushEnabled(true);
      }
    }
  }, []);

  // Ayarlar ykle ve uygula
  useEffect(() => {
    const applySettings = () => {
      const settings = loadSettings();

      // Tema ayar
      if (settings.appearance?.theme) {
        const theme = settings.appearance.theme;
        document.body.classList.remove('theme-light', 'theme-dark', 'theme-auto');

        if (theme === 'auto') {
          // Sistem tercihini kontrol et
          const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
          document.body.classList.add(prefersDark ? 'theme-dark' : 'theme-light');
        } else {
          document.body.classList.add(`theme-${theme}`);
        }
      }

      // Renk emas
      if (settings.appearance?.colorScheme) {
        document.body.classList.remove('color-green', 'color-blue', 'color-brown', 'color-purple');
        document.body.classList.add(`color-${settings.appearance.colorScheme}`);
      }

      // Yaz boyutu
      if (settings.appearance?.fontSize) {
        document.body.classList.remove('font-small', 'font-medium', 'font-large');
        document.body.classList.add(`font-${settings.appearance.fontSize}`);
      }
    };

    applySettings();

    // Settings deitiinde tekrar uygula
    const handleSettingsChange = () => applySettings();
    window.addEventListener('storage', handleSettingsChange);
    window.addEventListener('sg-settings-changed', handleSettingsChange);

    return () => {
      window.removeEventListener('storage', handleSettingsChange);
      window.removeEventListener('sg-settings-changed', handleSettingsChange);
    };
  }, []);

  // Tab deitiinde localStorage'a kaydet
  useEffect(() => {
    localStorage.setItem('sg_current_tab', tab);
  }, [tab]);

  const handleLogin = (jwtToken, user) => {
    setToken(jwtToken);
    setUsername(user);
    localStorage.setItem('sg_token', jwtToken);
    localStorage.setItem('sg_username', user);
  };

  const handleLogout = () => {
    setToken(null);
    setUsername('');
    localStorage.removeItem('sg_token');
    localStorage.removeItem('sg_username');
  };

  // Tarayc tespiti
  const detectBrowser = () => {
    const userAgent = navigator.userAgent.toLowerCase();
    if (userAgent.includes('edg/')) return 'edge';
    if (userAgent.includes('opr/') || userAgent.includes('opera')) return 'opera';
    if (userAgent.includes('chrome')) return 'chrome';
    if (userAgent.includes('safari') && !userAgent.includes('chrome')) return 'safari';
    if (userAgent.includes('firefox')) return 'firefox';
    return 'unknown';
  };

  const subscribeToPush = async () => {
    setPushError('');

    const browser = detectBrowser();
    console.log('Tespit edilen tarayc:', browser);

    // Tarayc destei kontrol
    if (!('serviceWorker' in navigator)) {
      setPushError('Taraycn Service Worker desteklemiyor.');
      return;
    }

    if (typeof Notification === 'undefined') {
      setPushError('Taraycn bildirim zelliini desteklemiyor.');
      return;
    }

    // Safari zel kontrol
    if (browser === 'safari') {
      if (!('pushManager' in ServiceWorkerRegistration.prototype)) {
        setPushError('Safari taraycnzda Push API desteklenmiyor. macOS 12.1+ veya iOS 16.4+ gereklidir.');
        return;
      }
    }

    try {
      // Bildirim izni isteme (taraycya zel)
      let permission;

      if (browser === 'safari') {
        // Safari iin zel izin kontrol
        if (window.safari && window.safari.pushNotification) {
          // Eski Safari push notification API
          setPushError('Safari iin Web Push henz tam desteklenmiyor. Ltfen Chrome, Firefox veya Edge kullann.');
          return;
        }
        permission = await Notification.requestPermission();
      } else {
        // Chrome, Firefox, Edge, Opera iin standart
        permission = await Notification.requestPermission();
      }

      if (permission !== 'granted') {
        setPushError('Bildirim izni verilmedi. Tarayc ayarlarndan izin verebilirsiniz.');
        return;
      }

      // Service Worker kaydn bekle
      const registration = await navigator.serviceWorker.ready;

      // Mevcut abonelii kontrol et
      let subscription = await registration.pushManager.getSubscription();

      if (subscription) {
        console.log('Mevcut abonelik bulundu, gncelleniyor...');
      }

      // Push Manager abonelii olutur (taraycya zel ayarlar)
      const subscribeOptions = {
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(VAPID_PUBLIC_KEY)
      };

      // Firefox iin ek ayarlar
      if (browser === 'firefox') {
        console.log('Firefox iin push ayarlar yaplandrlyor...');
      }

      // Chrome/Edge/Opera iin ek ayarlar
      if (browser === 'chrome' || browser === 'edge' || browser === 'opera') {
        console.log(`${browser.toUpperCase()} iin push ayarlar yaplandrlyor...`);
      }

      subscription = await registration.pushManager.subscribe(subscribeOptions);

      // Sunucuya gnder
      const res = await fetch(`${API_URL}/push/subscribe`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify({
          ...subscription.toJSON(),
          browser: browser,
          userAgent: navigator.userAgent
        })
      });

      const data = await res.json().catch(() => ({}));
      if (!res.ok) {
        throw new Error(
          data.message || 'Sunucuya push abonelii kaydedilemedi.'
        );
      }

      setPushEnabled(true);
      console.log(`${browser.toUpperCase()} iin push bildirimleri baaryla etkinletirildi!`);

      // Toplam abonelik says
      const totalSubs = data.totalSubscriptions || 1;
      console.log(`Toplam aktif abonelik: ${totalSubs}`);

      // Test bildirimi gnder (taraycya zel)
      let successMessage = '';
      if (browser === 'firefox') {
        successMessage = `Firefox iin bildirimler etkinletirildi!`;
      } else if (browser === 'safari') {
        successMessage = `Safari iin bildirimler etkinletirildi!`;
      } else {
        successMessage = `${browser.toUpperCase()} iin bildirimler etkinletirildi!`;
      }

      if (totalSubs > 1) {
        successMessage += `\n\nToplam ${totalSubs} cihaz/taraycda bildirimler aktif.`;
      }

      alert(successMessage);

    } catch (err) {
      console.error('Push abonelik hatas:', err);

      // Taraycya zel hata mesajlar
      let errorMessage = 'Push abonelii yaplamad.';

      if (browser === 'firefox' && err.message.includes('subscription')) {
        errorMessage = 'Firefox iin push abonelii oluturulamad. Tarayc ayarlarnz kontrol edin.';
      } else if (browser === 'safari') {
        errorMessage = 'Safari iin push destei snrldr. Chrome veya Firefox kullanmanz neririz.';
      } else if (err.message.includes('permissions')) {
        errorMessage = 'Bildirim izinleri reddedildi. Tarayc ayarlarndan izin vermelisiniz.';
      } else if (err.message) {
        errorMessage = err.message;
      }

      setPushError(errorMessage);
    }
  };

  if (!token) {
    return <Login onLogin={handleLogin} />;
  }

  return (
    <div className="app">
     <header className="app-header">
  <div className="header-left">
   <div className="logo-box" onClick={() => setTab('home')} style={{ cursor: 'pointer' }}>
 <img
  src={`${API_URL.replace('/api', '')}/uploads/logo.png`}
  alt="Akll Bahe"
  className="app-logo"
/>
</div>

  </div>
    
  <div className="header-right">
    <div className="mobile-header-info">
  <WeatherWidgeth token={token} />
</div>

<button
  type="button"
  className="tabs-hamburger"
  onClick={() => setMobileTabsOpen((open) => !open)}
>
  
</button>
 
    {/* --- HAVA DURUMU + SAAT YANYANA --- */}
    <div className="weather-clock-row">
      <WeatherWidget token={token} />

       
    </div>

    {/* --- k + Bildirimler Butonlar --- */}
    <div className="user-controls-row">

      <button className="btn icon-btn" onClick={handleLogout}>
        <span className="btn-icon"></span>
        <span>k Yap</span>
      </button>

      {typeof Notification !== 'undefined' && 'serviceWorker' in navigator && (
        <button
          className="btn icon-btn"
          onClick={subscribeToPush}
          disabled={pushEnabled}
        >
          <span className="btn-icon"></span>
          <span>{pushEnabled ? 'Bildirimler ak' : 'Bildirimleri a'}</span>
        </button>
      )}
    </div>
  </div>
</header>


     {pushError && <p className="error-text">{pushError}</p>}

<div className="tabs-wrapper">
   
  {/* Mobil alr men (sekme listesi + en altta k/bildirim) */}
  {mobileTabsOpen && (
    <div className="tabs-mobile-menu">
      <div className="tabs-mobile-items">
        <button
          className={`tabs-mobile-item ${tab === 'home' ? 'active' : ''}`}
          onClick={() => {
            setTab('home');
            setMobileTabsOpen(false);
          }}
        >
          Home
        </button>
        <button
          className={`tabs-mobile-item ${tab === 'trees' ? 'active' : ''}`}
          onClick={() => {
            setTab('trees');
            setMobileTabsOpen(false);
          }}
        >
          Aalar
        </button>
        <button
          className={`tabs-mobile-item ${tab === 'vegetables' ? 'active' : ''}`}
          onClick={() => {
            setTab('vegetables');
            setMobileTabsOpen(false);
          }}
        >
          Sebzeler
        </button>
        <button
          className={`tabs-mobile-item ${
            tab === 'reminders' ? 'active' : ''
          }`}
          onClick={() => {
            setTab('reminders');
            setMobileTabsOpen(false);
          }}
        >
          Hatrlatmalar
        </button>
        <button
          className={`tabs-mobile-item ${
            tab === 'calendar' ? 'active' : ''
          }`}
          onClick={() => {
            setTab('calendar');
            setMobileTabsOpen(false);
          }}
        >
          Takvim
        </button>
        <button
          className={`tabs-mobile-item ${tab === 'reports' ? 'active' : ''}`}
          onClick={() => {
            setTab('reports');
            setMobileTabsOpen(false);
          }}
        >
          Raporlar
        </button>
         <button
          className={`tabs-mobile-item ${tab === 'weather' ? 'active' : ''}`}
          onClick={() => {
            setTab('weather');
            setMobileTabsOpen(false);
          }}
        >
          Hava Durumu
        </button>
        <button
          className={`tabs-mobile-item ${tab === 'settings' ? 'active' : ''}`}
          onClick={() => {
            setTab('settings');
            setMobileTabsOpen(false);
          }}
        >
          Ayarlar
        </button>
      </div>

      {/*  Mobilde MENN EN ALTINDA IKI & BLDRM */}
      <div className="tabs-mobile-footer">
        <button
          className="btn icon-btn"
          onClick={() => {
            handleLogout();
            setMobileTabsOpen(false);
          }}
        >
          <span className="btn-icon"></span>
          <span>k Yap</span>
        </button>

        {typeof Notification !== 'undefined' && 'serviceWorker' in navigator && (
          <button
            className="btn icon-btn"
            onClick={() => {
              subscribeToPush();
              setMobileTabsOpen(false);
            }}
            disabled={pushEnabled}
          >
            <span className="btn-icon"></span>
            <span>
              {pushEnabled ? 'Bildirimler ak' : 'Bildirimleri a'}
            </span>
          </button>
        )}
      </div>
    </div>
  )}
</div>

{/* Desktopta grnen normal sekmeler */}
 



      <nav className="tabs">
        <button
          className={`tab ${tab === 'home' ? 'active' : ''}`}
          onClick={() => setTab('home')}
        >
          Home
        </button>
        <button
          className={`tab ${tab === 'trees' ? 'active' : ''}`}
          onClick={() => setTab('trees')}
        >
          Aalar
        </button>
        <button
          className={`tab ${tab === 'vegetables' ? 'active' : ''}`}
          onClick={() => setTab('vegetables')}
        >
          Sebzeler
        </button>
        <button
          className={`tab ${tab === 'reminders' ? 'active' : ''}`}
          onClick={() => setTab('reminders')}
        >
          Hatrlatmalar
        </button>
        <button
          className={`tab ${tab === 'calendar' ? 'active' : ''}`}
          onClick={() => setTab('calendar')}
        >
          Takvim
        </button>
        <button
          className={`tab ${tab === 'reports' ? 'active' : ''}`}
          onClick={() => setTab('reports')}
        >
          Raporlar
        </button>
        <button
          className={`tab ${tab === 'weather' ? 'active' : ''}`}
          onClick={() => setTab('weather')}
        >
          Hava Durumu
        </button>
        <button
          className={`tab ${tab === 'settings' ? 'active' : ''}`}
          onClick={() => setTab('settings')}
        >
          Ayarlar
        </button>
      </nav>

     <main className="app-main">
  {tab === 'home' && (
    <div className="tab-panel">
      <Home token={token} />
    </div>
  )}
  {tab === 'trees' && (
    <div className="tab-panel">
      <TreeManager token={token} />
    </div>
  )}
  {tab === 'vegetables' && (
    <div className="tab-panel">
      <VegetableManager token={token} />
    </div>
  )}
  {tab === 'reminders' && (
    <div className="tab-panel">
      <Reminders
        token={token}
        month={reminderMonth}
        onChangeMonth={setReminderMonth}
      />
    </div>
  )}
  {tab === 'calendar' && (
    <div className="tab-panel">
      <CalendarView
        token={token}
        onSelectMonth={(m) => {
          setReminderMonth(m);
          setTab('reminders');
        }}
      />
    </div>
  )}
  {tab === 'reports' && (
    <div className="tab-panel">
      <Reports token={token} />
    </div>
  )}
  {tab === 'weather' && (
    <div className="tab-panel">
      <WeatherTab token={token} />
    </div>
  )}



  {tab === 'settings' && (
    <div className="tab-panel">
      <Settings token={token} />
    </div>
  )}
</main>
    </div>
  );
}


export default App;
